                            //     callback(ticket, res);
                            // }, 1000)
                            // return 'loading';
                            // return res
                            return `
                                <div>
                                    <span style="color: #17E1FF;">  ${params[0].name}  </span>
                                    <br>
                                    平衡率 : <span style="color: ${params[0].value>target2?"#0CED94":"#FC3E3F"};"> ${params[0].value} %</span>
                                    <br>
                                    瓶颈站 : ${alertOneList[params[0].dataIndex]}
                                    <br>
                                    ${spaceNbsp}${alertTwoList[params[0].dataIndex]}
                                    <br>
                                    瓶颈CT : ${alertThreeList[params[0].dataIndex]}
                                    <br>
                                </div>
                            `
                        }
                    },
                    xAxis: [{
                        type: 'category',
                        boundaryGap: true,//折线图在x轴的起始点是否贴y轴
                        axisLine: { //坐标轴轴线相关设置。数学上的x轴
                            show: true,
                            lineStyle: {
                                color: '#233653' //x轴颜色
                            },
                        },
                        axisTick:{
                            alignWithLabel: true,//让图表在刻度线的刻度上

                            show:true, // 不显示坐标轴刻度线
                            length:5,
                            // color:'#fff',
                            lineStyle: {
                                color: '#D9D9D9'//x轴刻度线颜色
                            },
                        },
                        axisLabel: { //坐标轴刻度标签的相关设置
                            // margin: 6,
                            textStyle: {
                                color: '#36E5F3',
                                fontSize: 16
                            },
                            formatter: function(data) {
                                return data
                            }
                        },
                        splitLine: {
                            show: false,
                        },
                        data: xLabel
                    }],
                    yAxis: [{
                        // min: 0,
                        // name:'单位:PCS',
                        // nameLocation:'end',// x轴name处于x轴的什么位置
                        // nameTextStyle: { // x轴name的样式调整
                        //     color: '#a1abc0',
                        //     fontSize: this.setFontSize(10),
                        //     padding:[0,20,-5,-10]  // 加上padding可以调整其位置
                        // },
                        splitLine: {
                            show: true,
                            lineStyle: {
                                color: '#233653'
                            },
                        },
                        axisLine: {
                            show: false,
                        },
                        axisLabel: {
                            show: true,
                            color:'#a1abc0',
                            fontSize:16,
                            // 两位小数
                            formatter:function(value,index){
                                return value.toFixed(2) + '%'   
                            }
                        },
                        axisTick: {
                            show: false,
                        },
                    }],
                    series: [ {
                        name: '平衡率',
                        // 
                        type: 'line',
                        symbol: 'circle', // 默认是空心圆（中间是白色的），改成实心圆
                        showAllSymbol: true,
                        symbolSize:10,//拐点大小
                        smooth: true, // 是否是平滑曲线
                        // 目标线
                        markLine: {
                            symbol: ["none", "none"],
                            label: {
                                show:false,
                                color: "#17DBCC",
                                symbol: "none",
                                position: "end",
                                distance: -20,
                                fontSize: 12,
                                // 目标值设置
                                formatter: "{c}%",
                            },
                            data: [
                                {
                                    name: "Base Line1",
                                    silent: false,
                                    // 目标值 变量 target
                                    yAxis: target1,
                                    lineStyle: {
                                        type: "solid",
                                        color: "#17DBCC",
                                        width: 1,
                                    },
                                },
                                {
                                    name: "Base Line2",
                                    silent: false,
                                    // 目标值 变量 target
                                    yAxis: target2,
                                    lineStyle: {
                                        type: "solid",
                                        color: "#FC3E3F",
                                        width: 1,
                                    },
                                },
                            ],
                        },
                        lineStyle: {
                            normal: {
                                width: 4,
                                color: "#17DBCC", // 线条颜色
                            },
                        },
                        // 折线图 拐点 圆点颜色样式
                        itemStyle: {
                            normal: {
                                // 柱子颜色
                                color: function (params) {
                                    // console.log(params);
                                    // 定义的目标值 接口 target
                                    if(params.data>=target1){
                                        return "#0CEE92"
                                    }else{
                                        return "#FC3E3F"
                                    }
                                    // return colors[params.dataIndex % 6];
                                },
                                
                                // color: "#0CEE92",
                                borderWidth:3,
                                borderColor:"#fff"
                            },
                        },
                        // 折线 头部 上方文字
                        label: {
                            show: true,
                            position: [20, -20],
                            // 顶部文字的颜色
                            // color: 'pink',

                            fontSize: 16,
                            align: 'center',
                            // formatter: "{c}%",
                            //顶部文字的颜色 根据顶部数据的大小，判定设置不同的颜色
                            formatter: params=>{
                                // console.log(params);
                                // 定义的目标值 接口 target
                                if(params.data>=target1){
                                    return `{big|${params.data}%}`
                                }else{
                                    return `{little|${params.data}%}`
                                }

                            },
                            //设定顶部文字的颜色
                            rich:{
                                big:{
                                    color: '#17DBCC',
                                    fontSize: 16,
                                },
                                little:{
                                    color: '#FC3E3F',
                                    fontSize: 16,
                                }
                            }
                        },
                        //折线图 面积颜色
                        areaStyle: {
                            normal: {
                                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                    {
                                        offset: 0,
                                        color: 'rgba(12,237,148,0.4)'
                                    },
                                    {
                                        offset: 1,
                                        color: 'rgba(47,248,255,0)'
                                    }
                                ], false),
                            }
                        },
                        data: goOutSchool
                    }]
                };
                myChart.setOption(option, true);
                
                const chartObserver = new ResizeObserver(() => {
                    myChart.setOption(option, true);
                    myChart.resize();
                });
                chartObserver.observe(this.$refs.chartLineTwo);
            })
        },
    },
}
</script>

